.password-recover {
  justify-content: center;
  align-items: center;
  overflow: hidden;

  &-title {
    margin-bottom: 5rem;
  }

  &-description {
    text-align: center;
    letter-spacing: .25px;
    color: #9a9a9a;
    margin: 2.5rem 0;
    font-size: 1.4rem;
    font-weight: 400;
    line-height: 1.57;
  }

  .notification {
    padding: 15px;
    margin-bottom: 20px;
    border: 1px solid transparent;
    border-radius: 4px;
    width: 100%;
    max-width: 424px;
    font-size: 1.4rem;
  }

  .notification-success {
    color: #3c763d;
    background-color: #dff0d8;
    border-color: #d6e9c6;
  }

  .notification-error {
    color: #a94442;
    background-color: #f2dede;
    border-color: #ebccd1;
  }
}
